.whatsbutton {
  display: block;
  font-size: 14px;
  line-height: 20px;
  position: fixed;
  bottom: 24px;
  right: 24px;
  z-index: 9999;
}

.whatsbutton *,
.whatsbutton *:before,
.whatsbutton *:after {
  box-sizing: border-box;
}

.whatsbutton .wb-toggle-button {
  background-color: #25d366;
  border-radius: 100%;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  color: #fff;
  cursor: pointer;
  display: block;
  height: 56px;
  padding: 10px;
  position: absolute;
  bottom: 0;
  right: 0;
  width: 56px;
}

.whatsbutton .wb-toggle-button:before, .whatsbutton .wb-toggle-button:after {
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;
  content: "";
  display: block;
  height: 36px;
  position: absolute;
  left: 10px;
  top: 10px;
  transition: opacity 160ms ease-in-out, transform 160ms ease-in-out;
  width: 36px;
}

